.content {
  position: relative;
  flex: 1 1 100%;
  background-color: rgba(255, 255, 255, 0.801);
  border-radius: 20px;
}
.main_image img{
  border-radius: 20px;
  width: 100%;
  height: 220px;
}
.main{
  padding: 20px 0 20px 20px;
}
.icon{
  flex:0 0 100px;
}
.icon img{
  max-width: 100%;
}
.main_descripttion{
  padding: 10px 0 0 30px;
}
.posts_img img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.posts{
  display: flex;
  align-items: center;
  flex:0 0 400px;
}
.info_posts{
  padding: 0 0 0 15px;
}